"use client";
import { useState, useEffect } from "react";
import {
    Plus,
    RefreshCw,
    Trash2,
    X,
    Copy,
    Check,
    Link,
    Calendar,
} from "lucide-react";
import { env } from "@/lib/env";
import s from "./GeneralTab.module.css";

interface CalendarData {
    id: string;
    name: string;
    type: string;
    url?: string;
    events: { id: string; title: string }[];
    lastSync?: string;
}
interface Friend {
    id: string;
    user1: { id: string; email: string; name?: string };
    user2: { id: string; email: string; name?: string };
    status: string;
}

const TYPES = [
    {
        value: "ics_url",
        label: "üìÖ ICS URL",
        desc: "Import from any public ICS/iCal URL",
    },
    { value: "google", label: "üîµ Google Calendar", desc: "Coming soon" },
    { value: "apple", label: "üçé Apple Calendar", desc: "Coming soon" },
    { value: "proton", label: "üü£ Proton Calendar", desc: "Coming soon" },
];

function getUserId(): string | null {
    const token =
        typeof window !== "undefined" ? localStorage.getItem("token") : null;
    if (!token) return null;
    try {
        return JSON.parse(atob(token)).sub;
    } catch {
        return null;
    }
}

export default function GeneralTab() {
    const [calendars, setCalendars] = useState<CalendarData[]>([]);
    const [friends, setFriends] = useState<Friend[]>([]);
    const [loading, setLoading] = useState(true);
    const [syncingId, setSyncingId] = useState<string | null>(null);

    const [showImport, setShowImport] = useState(false);
    const [selType, setSelType] = useState<string | null>(null);
    const [impName, setImpName] = useState("");
    const [impUrl, setImpUrl] = useState("");
    const [importing, setImporting] = useState(false);
    const [impError, setImpError] = useState("");

    const [showExport, setShowExport] = useState(false);
    const [exportLink, setExportLink] = useState("");
    const [copied, setCopied] = useState(false);

    useEffect(() => {
        fetchAll();
    }, []);

    const api = (path: string, opts?: RequestInit) =>
        fetch(`${env.API_URL}${path}`, {
            ...opts,
            headers: {
                Authorization: `Bearer ${localStorage.getItem("token")}`,
                "Content-Type": "application/json",
                ...opts?.headers,
            },
        });

    const fetchAll = async () => {
        setLoading(true);
        const [calRes, frRes] = await Promise.all([
            api("/api/calendars"),
            api("/api/friends"),
        ]);
        if (calRes.ok) setCalendars(await calRes.json());
        if (frRes.ok) setFriends(await frRes.json());
        setLoading(false);
    };

    const closeImport = () => {
        setShowImport(false);
        setSelType(null);
        setImpName("");
        setImpUrl("");
        setImpError("");
    };

    const handleImport = async () => {
        if (!selType || !impName || (selType === "ics_url" && !impUrl)) return;
        setImporting(true);
        setImpError("");
        try {
            const res = await api("/api/calendars", {
                method: "POST",
                body: JSON.stringify({
                    name: impName,
                    type: selType,
                    url: impUrl || null,
                }),
            });
            if (!res.ok)
                throw new Error((await res.json()).error || "Import failed");
            closeImport();
            fetchAll();
        } catch (e) {
            setImpError(e instanceof Error ? e.message : "Import failed");
        } finally {
            setImporting(false);
        }
    };

    const handleSync = async (id: string) => {
        setSyncingId(id);
        await api(`/api/calendars/${id}/sync`, { method: "POST" });
        fetchAll();
        setSyncingId(null);
    };

    const handleDelete = async (id: string) => {
        if (!confirm("Delete this calendar and all its events?")) return;
        await api(`/api/calendars/${id}`, { method: "DELETE" });
        fetchAll();
    };

    const openExport = (friendId?: string) => {
        const token = localStorage.getItem("token");
        const link = friendId
            ? `${env.API_URL}/api/ics/my-calendar.ics?friendToken=${btoa(`${token}:${friendId}`)}`
            : `${env.API_URL}/api/ics/my-calendar.ics?token=${token}`;
        setExportLink(link);
        setShowExport(true);
        setCopied(false);
    };

    const copy = () => {
        navigator.clipboard.writeText(exportLink);
        setCopied(true);
        setTimeout(() => setCopied(false), 2000);
    };

    const uid = getUserId();
    const accepted = friends.filter((f) => f.status === "accepted");
    const totalEvents = calendars.reduce(
        (n, c) => n + (c.events?.length || 0),
        0,
    );

    if (loading)
        return (
            <div className={s.loading}>
                <div className={s.spinner} />
                <span>Loading‚Ä¶</span>
            </div>
        );

    return (
        <div className={s.page}>
            {/* Header */}
            <div className={s.pageHeader}>
                <h1 className={s.pageTitle}>Dashboard</h1>
                <div className={s.btnRow}>
                    <button
                        className={`${s.btn} ${s.btnSecondary}`}
                        onClick={() => openExport()}
                    >
                        <Link size={14} /> Export My Calendar
                    </button>
                    <button
                        className={`${s.btn} ${s.btnPrimary}`}
                        onClick={() => setShowImport(true)}
                    >
                        <Plus size={14} /> Import Calendar
                    </button>
                </div>
            </div>

            {/* Stats */}
            <div className={s.statsGrid}>
                {[
                    { label: "Calendars", value: calendars.length },
                    { label: "Total Events", value: totalEvents },
                    { label: "Friends", value: accepted.length },
                ].map(({ label, value }) => (
                    <div key={label} className={s.statCard}>
                        <div className={s.statValue}>{value}</div>
                        <div className={s.statLabel}>{label}</div>
                    </div>
                ))}
            </div>

            {/* My Calendars */}
            <div className={s.section}>
                <div className={s.sectionTitle}>My Calendars</div>
                {calendars.length === 0 ? (
                    <div className={s.empty}>
                        <Calendar size={36} className={s.emptyIcon} />
                        <span className={s.emptyText}>No calendars yet</span>
                        <button
                            className={`${s.btn} ${s.btnPrimary}`}
                            onClick={() => setShowImport(true)}
                        >
                            <Plus size={13} /> Import Calendar
                        </button>
                    </div>
                ) : (
                    <div className={s.calList}>
                        {calendars.map((c) => (
                            <div key={c.id} className={s.calRow}>
                                <div>
                                    <div className={s.calName}>{c.name}</div>
                                    <div className={s.calMeta}>
                                        <span
                                            className={`${s.badge} ${s.badgePurple}`}
                                        >
                                            {c.type}
                                        </span>
                                        <span className={s.metaText}>
                                            {c.events?.length || 0} events
                                        </span>
                                        {c.lastSync && (
                                            <span className={s.metaText}>
                                                synced{" "}
                                                {new Date(
                                                    c.lastSync,
                                                ).toLocaleString()}
                                            </span>
                                        )}
                                    </div>
                                    {c.url && (
                                        <div className={s.calUrl}>{c.url}</div>
                                    )}
                                </div>
                                <div className={s.calActions}>
                                    {c.type === "ics_url" && (
                                        <button
                                            className={`${s.btn} ${s.btnSecondary} ${s.btnSm}`}
                                            onClick={() => handleSync(c.id)}
                                            disabled={syncingId === c.id}
                                        >
                                            <RefreshCw size={12} />
                                            {syncingId === c.id
                                                ? "Syncing‚Ä¶"
                                                : "Sync"}
                                        </button>
                                    )}
                                    <button
                                        className={`${s.btn} ${s.btnDanger} ${s.btnSm} ${s.btnIcon}`}
                                        onClick={() => handleDelete(c.id)}
                                    >
                                        <Trash2 size={13} />
                                    </button>
                                </div>
                            </div>
                        ))}
                    </div>
                )}
            </div>

            {/* Share */}
            <div className={s.section}>
                <div className={s.sectionTitle}>Share Your Calendar</div>
                <div className={s.shareList}>
                    <div className={s.shareRow}>
                        <span className={s.shareRowName}>
                            üåê Anyone (public link)
                        </span>
                        <button
                            className={`${s.btn} ${s.btnSecondary} ${s.btnSm}`}
                            onClick={() => openExport()}
                        >
                            <Link size={12} /> Get Link
                        </button>
                    </div>
                    {accepted.length === 0 && (
                        <p
                            className={s.metaText}
                            style={{ padding: "0.5rem 0" }}
                        >
                            Add friends to generate per-friend links.
                        </p>
                    )}
                    {accepted.map((f) => {
                        const friend = f.user1.id === uid ? f.user2 : f.user1;
                        return (
                            <div key={f.id} className={s.shareRow}>
                                <span className={s.shareRowName}>
                                    {friend.name || friend.email}
                                </span>
                                <button
                                    className={`${s.btn} ${s.btnSecondary} ${s.btnSm}`}
                                    onClick={() => openExport(friend.id)}
                                >
                                    <Link size={12} /> Get Link
                                </button>
                            </div>
                        );
                    })}
                </div>
            </div>

            {/* Import Modal */}
            {showImport && (
                <div className={s.overlay} onClick={closeImport}>
                    <div
                        className={s.modal}
                        onClick={(e) => e.stopPropagation()}
                    >
                        <div className={s.modalHeader}>
                            <span className={s.modalTitle}>
                                Import Calendar
                            </span>
                            <button
                                className={s.closeBtn}
                                onClick={closeImport}
                            >
                                <X size={18} />
                            </button>
                        </div>

                        {!selType ? (
                            <div className={s.typeGrid}>
                                {TYPES.map((t) => (
                                    <button
                                        key={t.value}
                                        className={s.typeBtn}
                                        disabled={t.value !== "ics_url"}
                                        onClick={() =>
                                            t.value === "ics_url" &&
                                            setSelType(t.value)
                                        }
                                    >
                                        <div className={s.typeName}>
                                            {t.label}
                                        </div>
                                        <div className={s.typeDesc}>
                                            {t.desc}
                                        </div>
                                    </button>
                                ))}
                            </div>
                        ) : (
                            <div className={s.formStack}>
                                <button
                                    className={s.backBtn}
                                    onClick={() => setSelType(null)}
                                >
                                    ‚Üê Back
                                </button>
                                <div>
                                    <label className={s.fieldLabel}>
                                        Calendar Name
                                    </label>
                                    <input
                                        className={s.input}
                                        type="text"
                                        value={impName}
                                        onChange={(e) =>
                                            setImpName(e.target.value)
                                        }
                                        placeholder="My Calendar"
                                    />
                                </div>
                                <div>
                                    <label className={s.fieldLabel}>
                                        ICS URL
                                    </label>
                                    <input
                                        className={s.input}
                                        type="url"
                                        value={impUrl}
                                        onChange={(e) =>
                                            setImpUrl(e.target.value)
                                        }
                                        placeholder="https://example.com/calendar.ics"
                                    />
                                    <div className={s.hint}>
                                        Any public ICS/iCal URL (Google,
                                        Outlook, Fastmail‚Ä¶)
                                    </div>
                                </div>
                                {impError && (
                                    <div className={s.error}>{impError}</div>
                                )}
                                <div className={s.formRow}>
                                    <button
                                        className={`${s.btn} ${s.btnPrimary}`}
                                        style={{ flex: 1 }}
                                        onClick={handleImport}
                                        disabled={
                                            importing || !impName || !impUrl
                                        }
                                    >
                                        {importing ? "Importing‚Ä¶" : "Import"}
                                    </button>
                                    <button
                                        className={`${s.btn} ${s.btnSecondary}`}
                                        onClick={closeImport}
                                    >
                                        Cancel
                                    </button>
                                </div>
                            </div>
                        )}
                    </div>
                </div>
            )}

            {/* Export Modal */}
            {showExport && (
                <div className={s.overlay} onClick={() => setShowExport(false)}>
                    <div
                        className={s.modal}
                        onClick={(e) => e.stopPropagation()}
                    >
                        <div className={s.modalHeader}>
                            <span className={s.modalTitle}>Calendar Link</span>
                            <button
                                className={s.closeBtn}
                                onClick={() => setShowExport(false)}
                            >
                                <X size={18} />
                            </button>
                        </div>
                        <p className={s.hint} style={{ marginBottom: "1rem" }}>
                            Subscribe to this URL in any calendar app. It stays
                            live and always up to date.
                        </p>
                        <div className={s.linkRow}>
                            <input
                                readOnly
                                className={s.linkInput}
                                value={exportLink}
                            />
                            <button
                                className={`${s.btn} ${s.btnPrimary} ${s.btnIcon}`}
                                onClick={copy}
                            >
                                {copied ? (
                                    <Check size={15} />
                                ) : (
                                    <Copy size={15} />
                                )}
                            </button>
                        </div>
                        {copied && (
                            <div className={s.copiedMsg}>
                                ‚úì Copied to clipboard
                            </div>
                        )}
                    </div>
                </div>
            )}
        </div>
    );
}
