"use client";

import { useState, useEffect } from "react";
import { UserPlus, Users, Check, X, Trash2, Share2 } from "lucide-react";
import { env } from "@/lib/env";
import { getCurrentUserId } from "@/lib/utils";

interface Friend {
    id: string;
    user1: { id: string; email: string; name?: string };
    user2: { id: string; email: string; name?: string };
    status: string;
    sharedCalendars?: boolean;
}

export default function FriendsTab() {
    const [friends, setFriends] = useState<Friend[]>([]);
    const [showAddFriend, setShowAddFriend] = useState(false);
    const [friendEmail, setFriendEmail] = useState("");
    const [isLoading, setIsLoading] = useState(false);
    const [addError, setAddError] = useState("");

    useEffect(() => {
        fetchFriends();
    }, []);

    const fetchFriends = async () => {
        try {
            const token = localStorage.getItem("token");
            const res = await fetch(`${env.API_URL}/api/friends`, {
                headers: { Authorization: `Bearer ${token}` },
            });
            if (res.ok) setFriends(await res.json());
        } catch (e) {
            console.error(e);
        }
    };

    const handleAddFriend = async () => {
        if (!friendEmail) return;
        setIsLoading(true);
        setAddError("");
        try {
            const token = localStorage.getItem("token");
            const res = await fetch(`${env.API_URL}/api/friends/request`, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    Authorization: `Bearer ${token}`,
                },
                body: JSON.stringify({ userId: friendEmail }),
            });
            if (!res.ok)
                throw new Error(
                    (await res.json()).error || "Failed to send request",
                );
            setShowAddFriend(false);
            setFriendEmail("");
            fetchFriends();
        } catch (e) {
            setAddError(e instanceof Error ? e.message : "Failed");
        } finally {
            setIsLoading(false);
        }
    };

    const handleAccept = async (id: string) => {
        const token = localStorage.getItem("token");
        await fetch(`${env.API_URL}/api/friends/${id}/accept`, {
            method: "POST",
            headers: { Authorization: `Bearer ${token}` },
        });
        fetchFriends();
    };

    const handleRemove = async (id: string) => {
        if (!confirm("Remove this friend?")) return;
        const token = localStorage.getItem("token");
        await fetch(`${env.API_URL}/api/friends/${id}`, {
            method: "DELETE",
            headers: { Authorization: `Bearer ${token}` },
        });
        fetchFriends();
    };

    const handleToggleShare = async (
        friendshipId: string,
        currentlySharing: boolean,
    ) => {
        const token = localStorage.getItem("token");
        await fetch(
            `${env.API_URL}/api/friends/${friendshipId}/share-calendar`,
            {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    Authorization: `Bearer ${token}`,
                },
                body: JSON.stringify({ share: !currentlySharing }),
            },
        );
        fetchFriends();
    };

    const currentUserId = getCurrentUserId();

    const getFriend = (f: Friend) =>
        f.user1.id === currentUserId ? f.user2 : f.user1;

    const pending = friends.filter((f) => f.status === "pending");
    const accepted = friends.filter((f) => f.status === "accepted");
    const incoming = pending.filter((f) => f.user2.id === currentUserId);
    const outgoing = pending.filter((f) => f.user1.id === currentUserId);

    return (
        <div
            style={{ display: "flex", flexDirection: "column", gap: "1.5rem" }}
        >
            {/* ‚îÄ‚îÄ Header ‚îÄ‚îÄ */}
            <div
                style={{
                    display: "flex",
                    justifyContent: "space-between",
                    alignItems: "center",
                }}
            >
                <h2 style={{ fontSize: "1.5rem", fontWeight: "600" }}>
                    üë• Friends
                </h2>
                <button
                    onClick={() => setShowAddFriend(true)}
                    className="button button-primary"
                >
                    <UserPlus
                        style={{
                            width: "1rem",
                            height: "1rem",
                            marginRight: "0.5rem",
                        }}
                    />
                    Add Friend
                </button>
            </div>

            {/* ‚îÄ‚îÄ Incoming Requests ‚îÄ‚îÄ */}
            {incoming.length > 0 && (
                <div className="card">
                    <h3
                        style={{
                            fontWeight: "600",
                            marginBottom: "1rem",
                            fontSize: "1rem",
                        }}
                    >
                        üì¨ Friend Requests ({incoming.length})
                    </h3>
                    <div
                        style={{
                            display: "flex",
                            flexDirection: "column",
                            gap: "0.75rem",
                        }}
                    >
                        {incoming.map((f) => {
                            const friend = getFriend(f);
                            return (
                                <div
                                    key={f.id}
                                    style={{
                                        display: "flex",
                                        justifyContent: "space-between",
                                        alignItems: "center",
                                        padding: "0.75rem",
                                        border: "1px solid rgb(229,231,235)",
                                        borderRadius: "0.5rem",
                                    }}
                                >
                                    <div>
                                        <div style={{ fontWeight: "600" }}>
                                            {friend.name || friend.email}
                                        </div>
                                        <div
                                            style={{
                                                fontSize: "0.75rem",
                                                color: "rgb(107,114,128)",
                                            }}
                                        >
                                            {friend.email}
                                        </div>
                                    </div>
                                    <div
                                        style={{
                                            display: "flex",
                                            gap: "0.5rem",
                                        }}
                                    >
                                        <button
                                            onClick={() => handleAccept(f.id)}
                                            className="button button-success"
                                            style={{
                                                padding: "0.375rem 0.75rem",
                                                fontSize: "0.75rem",
                                            }}
                                        >
                                            <Check
                                                style={{
                                                    width: "0.75rem",
                                                    height: "0.75rem",
                                                    marginRight: "0.25rem",
                                                }}
                                            />
                                            Accept
                                        </button>
                                        <button
                                            onClick={() => handleRemove(f.id)}
                                            className="button button-danger"
                                            style={{
                                                padding: "0.375rem 0.75rem",
                                                fontSize: "0.75rem",
                                            }}
                                        >
                                            <X
                                                style={{
                                                    width: "0.75rem",
                                                    height: "0.75rem",
                                                }}
                                            />
                                        </button>
                                    </div>
                                </div>
                            );
                        })}
                    </div>
                </div>
            )}

            {/* ‚îÄ‚îÄ Friends List ‚îÄ‚îÄ */}
            <div className="card">
                <h3
                    style={{
                        fontWeight: "600",
                        marginBottom: "1rem",
                        fontSize: "1rem",
                    }}
                >
                    ‚úÖ Friends ({accepted.length})
                </h3>
                {accepted.length === 0 ? (
                    <div
                        style={{
                            textAlign: "center",
                            padding: "2rem",
                            color: "rgb(107,114,128)",
                        }}
                    >
                        <Users
                            size={40}
                            style={{
                                margin: "0 auto 0.75rem",
                                color: "rgb(209,213,219)",
                            }}
                        />
                        <p>No friends yet</p>
                    </div>
                ) : (
                    <div
                        style={{
                            display: "flex",
                            flexDirection: "column",
                            gap: "0.75rem",
                        }}
                    >
                        {accepted.map((f) => {
                            const friend = getFriend(f);
                            return (
                                <div
                                    key={f.id}
                                    style={{
                                        display: "flex",
                                        justifyContent: "space-between",
                                        alignItems: "center",
                                        padding: "0.75rem",
                                        border: "1px solid rgb(229,231,235)",
                                        borderRadius: "0.5rem",
                                    }}
                                >
                                    <div>
                                        <div style={{ fontWeight: "600" }}>
                                            {friend.name || friend.email}
                                        </div>
                                        <div
                                            style={{
                                                fontSize: "0.75rem",
                                                color: "rgb(107,114,128)",
                                                marginTop: "0.2rem",
                                            }}
                                        >
                                            {f.sharedCalendars ? (
                                                <span className="badge badge-green">
                                                    üìÖ Sharing calendar
                                                </span>
                                            ) : (
                                                <span
                                                    className="badge"
                                                    style={{
                                                        background:
                                                            "rgb(243,244,246)",
                                                        color: "rgb(107,114,128)",
                                                    }}
                                                >
                                                    Not sharing
                                                </span>
                                            )}
                                        </div>
                                    </div>
                                    <div
                                        style={{
                                            display: "flex",
                                            gap: "0.5rem",
                                        }}
                                    >
                                        <button
                                            onClick={() =>
                                                handleToggleShare(
                                                    f.id,
                                                    !!f.sharedCalendars,
                                                )
                                            }
                                            className={
                                                f.sharedCalendars
                                                    ? "button button-secondary"
                                                    : "button button-success"
                                            }
                                            style={{
                                                padding: "0.375rem 0.75rem",
                                                fontSize: "0.75rem",
                                            }}
                                        >
                                            <Share2
                                                style={{
                                                    width: "0.75rem",
                                                    height: "0.75rem",
                                                    marginRight: "0.25rem",
                                                }}
                                            />
                                            {f.sharedCalendars
                                                ? "Unshare"
                                                : "Share Calendar"}
                                        </button>
                                        <button
                                            onClick={() => handleRemove(f.id)}
                                            className="button button-danger"
                                            style={{
                                                padding: "0.375rem 0.75rem",
                                                fontSize: "0.75rem",
                                            }}
                                        >
                                            <Trash2
                                                style={{
                                                    width: "0.75rem",
                                                    height: "0.75rem",
                                                }}
                                            />
                                        </button>
                                    </div>
                                </div>
                            );
                        })}
                    </div>
                )}
            </div>

            {/* ‚îÄ‚îÄ Outgoing Requests ‚îÄ‚îÄ */}
            {outgoing.length > 0 && (
                <div className="card">
                    <h3
                        style={{
                            fontWeight: "600",
                            marginBottom: "1rem",
                            fontSize: "1rem",
                        }}
                    >
                        ‚è≥ Sent Requests
                    </h3>
                    <div
                        style={{
                            display: "flex",
                            flexDirection: "column",
                            gap: "0.75rem",
                        }}
                    >
                        {outgoing.map((f) => {
                            const friend = getFriend(f);
                            return (
                                <div
                                    key={f.id}
                                    style={{
                                        display: "flex",
                                        justifyContent: "space-between",
                                        alignItems: "center",
                                        padding: "0.75rem",
                                        border: "1px solid rgb(229,231,235)",
                                        borderRadius: "0.5rem",
                                    }}
                                >
                                    <div>
                                        <div style={{ fontWeight: "600" }}>
                                            {friend.name || friend.email}
                                        </div>
                                        <div
                                            style={{
                                                fontSize: "0.75rem",
                                                color: "rgb(107,114,128)",
                                            }}
                                        >
                                            Awaiting response
                                        </div>
                                    </div>
                                    <button
                                        onClick={() => handleRemove(f.id)}
                                        className="button button-secondary"
                                        style={{
                                            padding: "0.375rem 0.75rem",
                                            fontSize: "0.75rem",
                                        }}
                                    >
                                        Cancel
                                    </button>
                                </div>
                            );
                        })}
                    </div>
                </div>
            )}

            {/* ‚îÄ‚îÄ Add Friend Modal ‚îÄ‚îÄ */}
            {showAddFriend && (
                <div
                    className="modal-overlay"
                    onClick={() => {
                        setShowAddFriend(false);
                        setAddError("");
                    }}
                >
                    <div
                        className="modal-content"
                        onClick={(e) => e.stopPropagation()}
                    >
                        <div
                            style={{
                                display: "flex",
                                justifyContent: "space-between",
                                alignItems: "center",
                                marginBottom: "1rem",
                            }}
                        >
                            <h3
                                style={{
                                    fontWeight: "600",
                                    fontSize: "1.25rem",
                                }}
                            >
                                Add Friend
                            </h3>
                            <button
                                onClick={() => setShowAddFriend(false)}
                                style={{
                                    background: "none",
                                    border: "none",
                                    cursor: "pointer",
                                }}
                            >
                                <X size={20} />
                            </button>
                        </div>
                        <label
                            style={{
                                display: "block",
                                fontWeight: "500",
                                marginBottom: "0.5rem",
                            }}
                        >
                            Friend's Email
                        </label>
                        <input
                            type="email"
                            value={friendEmail}
                            onChange={(e) => setFriendEmail(e.target.value)}
                            onKeyDown={(e) =>
                                e.key === "Enter" && handleAddFriend()
                            }
                            className="input"
                            placeholder="friend@example.com"
                            style={{ marginBottom: "0.75rem" }}
                        />
                        {addError && (
                            <div
                                style={{
                                    padding: "0.5rem 0.75rem",
                                    background: "rgb(254,226,226)",
                                    color: "rgb(153,27,27)",
                                    borderRadius: "0.375rem",
                                    fontSize: "0.875rem",
                                    marginBottom: "0.75rem",
                                }}
                            >
                                {addError}
                            </div>
                        )}
                        <div style={{ display: "flex", gap: "0.75rem" }}>
                            <button
                                onClick={handleAddFriend}
                                disabled={isLoading || !friendEmail}
                                className="button button-primary"
                                style={{ flex: 1 }}
                            >
                                {isLoading ? "Sending..." : "Send Request"}
                            </button>
                            <button
                                onClick={() => {
                                    setShowAddFriend(false);
                                    setAddError("");
                                }}
                                className="button button-secondary"
                            >
                                Cancel
                            </button>
                        </div>
                    </div>
                </div>
            )}
        </div>
    );
}
