"use client";

import { useState, useEffect } from "react";
import { Calendar, Download, Users, Clock } from "lucide-react";
import { env } from "@/lib/env";

interface Calendar {
    id: string;
    name: string;
    type: string;
    events: Array<{ id: string; title: string } | null>;
    lastSync?: string;
}

export default function GeneralTab() {
    const [calendars, setCalendars] = useState<Calendar[]>([]);
    const [isLoading, setIsLoading] = useState(true);

    useEffect(() => {
        fetchCalendars();
    }, []);

    const fetchCalendars = async () => {
        try {
            const token = localStorage.getItem("token");
            const response = await fetch(`${env.API_URL}/api/calendars`, {
                headers: {
                    Authorization: `Bearer ${token}`,
                },
            });

            if (response.ok) {
                const data = await response.json();
                setCalendars(data);
            }
        } catch (error) {
            console.error("Failed to fetch calendars:", error);
        } finally {
            setIsLoading(false);
        }
    };

    const totalEvents = calendars.reduce(
        (sum, cal) => sum + (cal.events?.length || 0),
        0,
    );

    if (isLoading) {
        return (
            <div
                style={{
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                    padding: "3rem",
                    color: "rgb(107, 114, 128)",
                }}
            >
                Loading...
            </div>
        );
    }

    return (
        <div
            style={{
                display: "grid",
                gridTemplateColumns: "repeat(auto-fit, minmax(300px, 1fr))",
                gap: "1.5rem",
            }}
        >
            <div className="card">
                <h3
                    style={{
                        fontSize: "1.25rem",
                        fontWeight: "600",
                        marginBottom: "1rem",
                        color: "rgb(17, 24, 39)",
                    }}
                >
                    üìä
                    {calendars.length} Calendars
                </h3>
                <div
                    style={{
                        display: "flex",
                        alignItems: "center",
                        gap: "0.5rem",
                        color: "rgb(34, 197, 94)",
                        fontSize: "1.5rem",
                        fontWeight: "700",
                    }}
                >
                    <Calendar size={24} />
                    {calendars.length}
                </div>
            </div>

            <div className="card">
                <h3
                    style={{
                        fontSize: "1.25rem",
                        fontWeight: "600",
                        marginBottom: "1rem",
                        color: "rgb(17, 24, 39)",
                    }}
                >
                    üéØ Total Events
                </h3>
                <div
                    style={{
                        display: "flex",
                        alignItems: "center",
                        gap: "0.5rem",
                        color: "rgb(34, 197, 94)",
                        fontSize: "1.5rem",
                        fontWeight: "700",
                    }}
                >
                    <Clock size={24} />
                    {totalEvents}
                </div>
            </div>

            <div className="card">
                <h3
                    style={{
                        fontSize: "1.25rem",
                        fontWeight: "600",
                        marginBottom: "1rem",
                        color: "rgb(17, 24, 39)",
                    }}
                >
                    üì± Exportable
                </h3>
                <div
                    style={{
                        display: "flex",
                        alignItems: "center",
                        gap: "0.5rem",
                        color: "rgb(147, 51, 234)",
                        fontSize: "1.5rem",
                        fontWeight: "700",
                    }}
                >
                    <Download size={24} />
                    {calendars.length}
                </div>
            </div>

            {calendars.length === 0 ? (
                <div
                    className="card"
                    style={{ textAlign: "center", padding: "3rem" }}
                >
                    <Calendar
                        size={64}
                        style={{
                            color: "rgb(209, 213, 219)",
                            marginBottom: "1rem",
                        }}
                    />
                    <h3
                        style={{
                            fontSize: "1.25rem",
                            fontWeight: "600",
                            marginBottom: "0.5rem",
                            color: "rgb(17, 24, 39)",
                        }}
                    >
                        No calendars imported yet
                    </h3>
                    <p
                        style={{
                            color: "rgb(107, 114, 128)",
                            marginBottom: "1.5rem",
                        }}
                    >
                        Click "Import Calendar" to get started
                    </p>
                </div>
            ) : (
                <div className="card">
                    <h3
                        style={{
                            fontSize: "1.25rem",
                            fontWeight: "600",
                            marginBottom: "1.5rem",
                            color: "rgb(17, 24, 39)",
                        }}
                    >
                        üìã Your Calendars
                    </h3>
                    <div
                        style={{
                            display: "flex",
                            flexDirection: "column",
                            gap: "1rem",
                        }}
                    >
                        {calendars.map((calendar) => (
                            <div
                                key={calendar.id}
                                style={{
                                    padding: "1rem",
                                    border: "1px solid rgb(229, 231, 235)",
                                    borderRadius: "0.375rem",
                                    backgroundColor: "rgb(249, 250, 251)",
                                }}
                            >
                                <div
                                    style={{
                                        display: "flex",
                                        justifyContent: "space-between",
                                        alignItems: "center",
                                        marginBottom: "0.5rem",
                                    }}
                                >
                                    <h4
                                        style={{
                                            fontWeight: "600",
                                            color: "rgb(17, 24, 39)",
                                        }}
                                    >
                                        {calendar.name}
                                    </h4>
                                    <span className="badge badge-blue">
                                        Type: {calendar.type}
                                    </span>
                                </div>
                                <div
                                    style={{
                                        display: "flex",
                                        justifyContent: "space-between",
                                        color: "rgb(107, 114, 128)",
                                        fontSize: "0.875rem",
                                    }}
                                >
                                    <span>
                                        Events: {calendar.events?.length || 0}
                                    </span>
                                    {calendar.lastSync && (
                                        <span>
                                            Last sync:{" "}
                                            {new Date(
                                                calendar.lastSync,
                                            ).toLocaleString()}
                                        </span>
                                    )}
                                </div>
                            </div>
                        ))}
                    </div>
                </div>
            )}
        </div>
    );
}
